@media screen and (max-device-width: 480px) {
  html,
  body {
    width: auto !important;
    overflow-x: hidden !important;
  }
}

.card-text {
  padding-top: 20px;
}

.font {
  font-family: "Lato", sans-serif;
}

.navbar {
  background-color: #2196f3;
}

@media screen and (max-device-width: 574px) {
  .navbar-brand {
    margin-left: 1rem;
  }
}

.jumbotron {
  background-image: url(/static/media/videoblocks-old-library-shelves_bn3eipdwb_thumbnail-full01.c83220ff.png);
  background-size: cover;
  margin-bottom: 0;
  border-radius: 0;
}

.display-3, .lead {
    color: white;
    text-align: center;
}
.ripple {
  background-position: center;
  transition: background 0.8s;
}
.ripple:hover {
  background: #47a7f5 radial-gradient(circle, transparent 1%, #47a7f5 1%) center/15000%;
}
.ripple:active {
  background-color: #6eb9f7;
  background-size: 100%;
  transition: background 0s;
}

/* Button style */
button {
  border: none;
  border-radius: 2px;
  padding: 12px 18px;
  font-size: 16px;
  cursor: pointer;
  color: white;
  background-color: #2196f3;
  box-shadow: 0 0 4px #999;
  outline: none;
  width: 6.5rem;
}
.form-control {
  margin-right: .5rem;
  border-radius: 0.25rem!important;
}

.input-group {
  margin: .5rem 0;
}

@media screen and (max-device-width: 574px){
  .form-control {
    margin-left: .5rem;
  }
  .ripple {
    margin-right: .5rem;
  }
}

